import { showLoadError } from "../errors";
import { MessageKind, showPageMessage } from "../alert";
import {
    CheckDeviceTest,
    CheckDeviceTestResponse,
    StartDeviceTestResponse,
    StateData,
} from "../data/types";
import { catchAsync } from "../help";
import * as moment from "moment";

export function initTestDevice(container: HTMLElement, stateData: StateData): void {
    const testContainer = container.querySelector(`#test-device-container`) as HTMLElement;
    if (!testContainer?.parentElement?.classList.contains("show")) {
        return;
    }
    checkInternetConnection(testContainer, stateData.inetState);
    checkWireguardConnection(testContainer, stateData.wgState);
    rigRunTests(testContainer);
}

function checkInternetConnection(container: HTMLElement, inetState: number): void {
    if (inetState) {
        return;
    }
    showPageMessage(
        `No internet access. Active internet connection is required to run tests.
      Please check the device's settings.`,
        MessageKind.Danger,
    );
    container.querySelector(`#run-tests-btn`)!.setAttribute("disabled", "disabled");
}

function checkWireguardConnection(container: HTMLElement, wgState: number): void {
    if (wgState !== 3) {
        showPageMessage(
            `Device has no access to the remote server for testing. Please register the device first,
      or restore its identity.`,
            MessageKind.Danger,
        );
        container.querySelector(`#run-tests-btn`)!.setAttribute("disabled", "disabled");
    }
}

function rigRunTests(container: HTMLElement): void {
    const handler = catchAsync(async () => {
        try {
            await runTests(container);
        } catch (e) {
            showLoadError((e as { errorMessage: string }).errorMessage);
        }
    });
    container.querySelector(`#run-tests-btn`)!.addEventListener("click", handler);
    container.querySelector(`#rerun-tests-btn`)!.addEventListener("click", handler);
}

async function runTests(container: HTMLElement): Promise<void> {
    container.classList.add("test-run");
    container.querySelector("#abort-run-tests-btn")!.classList.remove("d-none");
    container.querySelector("#rerun-tests-btn")!.classList.add("d-none");

    // set run tests date
    container.querySelector("#run-test-date")!.innerHTML = moment(new Date()).format(
        "YYYY-MM-DD HH:mm:ss",
    );

    // start test and get testRunId
    const testRunId = (
        (await (await fetch("./api/mf_test.json")).json()) as StartDeviceTestResponse
    ).testRunId;

    // check device tests
    const statuses: ("waiting" | "running" | "passed" | "failed" | "aborted")[] = [];
    const testCount = 1;
    await testDeviceProccess(container, testRunId, statuses, testCount);
    const interval = setInterval(async () => {
        await testDeviceProccess(container, testRunId, statuses, testCount, interval);
    }, 10000);

    return rigAbortTests(container, testRunId);
}

async function testDeviceProccess(
    container: HTMLElement,
    testRunId: string,
    statuses: ("waiting" | "running" | "passed" | "failed" | "aborted")[],
    testCount: number,
    interval?: number,
): Promise<void> {
    const test = await checkDeviceTest(testRunId);
    statuses.push(...test.tests.map((t) => t.result.status));

    setTestResult(container, test.tests, testCount);

    if (test.finishedOn || test.wasAborted) {
        if (interval) {
            clearInterval(interval);
        }
        setMainResult(container, statuses);
        container.querySelector("#abort-run-tests-btn")!.classList.add("d-none");
        container.querySelector("#rerun-tests-btn")!.classList.remove("d-none");
    }
}

function setTestResult(container: HTMLElement, tests: CheckDeviceTest[], testCount: number): void {
    const logListEl = container.querySelector("#tests-log-list") as HTMLElement;
    for (const test of tests) {
        const el = document.createElement("div");
        if (test.result.status === "failed") {
            el.classList.add("test-fail");
            el.innerHTML =
                `( ${test.result.status} ) Test ${testCount}: ${test.name}.` +
                `Error: ${test.result.error || ""}`;
            continue;
        }

        el.innerHTML = `( ${test.result.status} ) Test ${testCount}: ${test.name}.`;
        logListEl.appendChild(el);
    }

    testCount++;
}

function setMainResult(
    container: HTMLElement,
    statuses: ("waiting" | "running" | "passed" | "failed" | "aborted")[],
): void {
    container.classList.add("finished");
    const testStatusEl = container.querySelector("#tests-result-status") as HTMLElement;
    if (statuses.includes("failed")) {
        testStatusEl.innerHTML = "Failure";
        testStatusEl.classList.add("test-fail");
        return;
    }
    testStatusEl.innerHTML = "Success";
    testStatusEl.classList.add("test-ok");
}

function rigAbortTests(
    container: HTMLElement,
    testRunId: string,
    //   interval: number
): void {
    const abortTestBtn = container.querySelector(`#abort-run-tests-btn`) as HTMLElement;

    abortTestBtn.addEventListener(
        "click",
        catchAsync(async () => {
            // clearInterval(interval);
            await fetch(`./api/mf_test.json?testRunId=${testRunId}&abort=1`);
            container.querySelector(`#TESTS_LOG_CONTINER_ID`)!.classList.add("finished");
        }),
    );
}

async function checkDeviceTest(testRunId: string): Promise<CheckDeviceTestResponse> {
    return (await (
        await fetch(`./api/mf_test.json?testRunId=${testRunId}`)
    ).json()) as CheckDeviceTestResponse;
}
