export function getServerPathByServerName(serverName: string): string {
    let serverHost = "ocp.overvis.com";
    if (serverName !== "modbus.overvis.com") {
        serverHost = serverName;
    }

    return `https://${serverHost}`;
}

export function formatSizeUnits(bytes: number): string {
    let result = "";
    if (Number(bytes) >= 1073741824) {
        result = (Number(bytes) / 1073741824).toFixed(2) + " gB";
    } else if (Number(bytes) >= 1048576) {
        result = (Number(bytes) / 1048576).toFixed(2) + " MB";
    } else if (Number(bytes) >= 1024) {
        result = (Number(bytes) / 1024).toFixed(2) + " kB";
    } else {
        result = Number(bytes).toFixed(2) + " B";
    }
    return result;
}

export function getFormValues(form: HTMLFormElement): Record<string, unknown> {
    const fields = Array.from(form.querySelectorAll("[data-holds]"));
    const data: Record<string, unknown> = {};
    for (const field of fields) {
        const name = field.attributes.getNamedItem("data-holds")!.value;
        if (data[name] && !Array.isArray(data[name])) {
            const val = data[name];
            data[name] = [val];
        }

        let value;
        if (field instanceof HTMLInputElement && field.type === "checkbox") {
            value = field.checked ? 1 : 0;
        } else if (field instanceof HTMLInputElement || field instanceof HTMLSelectElement) {
            value = field.value;
        }

        if (Array.isArray(data[name])) {
            (data[name] as unknown[]).push(value);
        } else {
            data[name] = value;
        }
    }

    return data;
}

export function numberToHex8(value: number): string {
    return value.toString(16).padStart(2, "0");
}

export function numberToHex16(value: number): string {
    return `${numberToHex8(value >> 8)}:${numberToHex8(value & 255)}`;
}

export async function delay(ms: number): Promise<void> {
    return new Promise<void>((resolve) => window.setTimeout(resolve, ms));
}

