export function isValidIp(ip: string): boolean {
    return /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(
        ip,
    );
}

export function fillSelect(selectId: string, values: string[]): void {
    const listEl = document.querySelector(`#${selectId}`) as HTMLSelectElement;
    const value = listEl.value;

    listEl.innerHTML = "";

    const defaultOption = document.createElement("option");
    defaultOption.value = "";
    defaultOption.text = "Disabled";
    listEl.appendChild(defaultOption);

    const addOption = (val: string): void => {
        const option = document.createElement("option");
        option.value = val;
        option.text = val;
        option.selected = val === value;
        listEl.appendChild(option);
    };

    if (!values.includes(value) && value !== "") {
        addOption(value);
    }

    for (const item of values) {
        addOption(item);
    }
}

export function secondsToDhms(sec: number): string {
    const d = Math.floor(sec / (3600 * 24));
    const h = Math.floor((sec % (3600 * 24)) / 3600);
    const m = Math.floor((sec % 3600) / 60);
    const s = Math.floor(sec % 60);
    return `${d}d ${h}h ${m}m ${s}s`;
}

export function removeDuplicateUidsRanges(value: string): string {
    return value
        .split(",")
        .map((r) => {
            const uids = r.split("-").map((u) => u.trim());
            return uids[0] === uids[1] ? uids[0] : r;
        })
        .join(",");
}

export function rigChangeFields(form: HTMLFormElement): void {
    const elems = Array.from(form.querySelectorAll("[data-holds]"));
    for (const el of elems) {
        el.addEventListener("change", () => {
            el.classList.add("changed");
            form.querySelector('button[type="submit"]')?.removeAttribute("disabled");
        });
    }
}

export function clearChangedStatusFromForm(form: HTMLFormElement, isDisableBtn?: boolean): void {
    const elems = Array.from(form.querySelectorAll("[data-holds]"));
    for (const el of elems) {
        el.classList.remove("changed");
    }
    isDisableBtn &&
        form.querySelector('button[type="submit"]')?.setAttribute("disabled", "disabled");
}

export function catchAsync<A extends unknown[]>(
    f: (...args: A) => Promise<void>,
): (...args: A) => void {
    return (...args) => {
        // eslint-disable-next-line no-console
        f(...args).catch(console.error);
    };
}

export function getCookieValue(name: string): string | undefined {
    return document.cookie
        .split("; ")
        .find((row) => row.startsWith(`${name}=`))
        ?.split("=")[1];
}

export function isItProxy(): boolean {
    return getCookieValue("proxy") === "true";
}
