import { ControlLoadError } from "../errors";
import { loadStateData, loadStaticData } from "../data/load";
import { StateData, StaticData } from "../data/types";
import { initRestoreIdentity } from "./restore";
import { initRegisterDevice } from "./register";
import { initFinalize } from "./finalize";
import { initTestDevice } from "./test";
import { hideAllAlerts } from "../alert";

const MANUFACTURING_CONTAINER_ID = "manufacturer-container";

export async function initManufacturing(): Promise<void> {
    const errorControl = new ControlLoadError(3);
    let staticData: StaticData;
    let stateData: StateData;
    try {
        staticData = await loadStaticData(errorControl);
        stateData = await loadStateData(errorControl);
    } catch (e) {
        const text = (e as Error).message;
        errorControl.showError("load", text);
        return;
    }

    const menuItems = Array.from(document.querySelectorAll(`.manufacturer-menu-link-container`));
    if (staticData.mfOpen) {
        for (const item of menuItems) {
            item.classList.remove("d-none");
        }
    } else {
        for (const item of menuItems) {
            item.remove();
        }
    }

    const container = document.querySelector(`#${MANUFACTURING_CONTAINER_ID}`) as
        | HTMLElement
        | undefined;
    if (!container || !staticData.mfOpen) {
        return;
    }

    initRestoreIdentity(container, staticData, stateData);
    initRegisterDevice(container, staticData, stateData);
    initFinalize(container);
    initTestDevice(container, stateData);

    const tabPanel = document.querySelector("#settings-nav-menu") as HTMLElement;
    tabPanel.addEventListener("click", () => {
        hideAllAlerts();
        setTimeout(() => {
            initRestoreIdentity(container, staticData, stateData);
            initRegisterDevice(container, staticData, stateData);
            initFinalize(container);
            initTestDevice(container, stateData);
        }, 300);
    });
}
