import { formatSizeUnits } from "../../help";
import { StateData } from "../../data/types";
import { setNewValue } from ".";

export function updateTcpClients(data: StateData): void {
    updateTcpCliRxTx(
        data.mbtcpSrvRxAvg,
        data.mbtcpSrvRxTotal,
        data.mbtcpSrvTxAvg,
        data.mbtcpSrvTxTotal,
    );
    updateTcpClientsList(data.mbtcpSrvClientsList);
}

function updateTcpCliRxTx(
    mbtcpSrvRxAvg: number,
    mbtcpSrvRxTotal: number,
    mbtcpSrvTxAvg: number,
    mbtcpSrvTxTotal: number,
): void {
    const el = document.querySelector("#tcp-client-rx-tx");
    if (!el) {
        return;
    }

    const value = `RX ${formatSizeUnits(mbtcpSrvRxAvg / 60)}/s (${formatSizeUnits(
        mbtcpSrvRxTotal,
    )} total) |<wbr> TX ${formatSizeUnits(mbtcpSrvTxAvg / 60)}/s (${formatSizeUnits(
        mbtcpSrvTxTotal,
    )} total)`;
    setNewValue(el.querySelector(".value")!, value);
}

function updateTcpClientsList(mbtcpSrvClientsList: string[]): void {
    const rowEl = document.querySelector("#tcp-client-list");
    if (!rowEl) {
        return;
    }
    if (mbtcpSrvClientsList.length < 1 || mbtcpSrvClientsList[0] === "") {
        rowEl.querySelector(".value")!.innerHTML = "-";
        return;
    }
    rowEl.querySelector(".value")!.innerHTML = "";
    for (const client of mbtcpSrvClientsList) {
        const cliEl = document.createElement("div");
        cliEl.append(client);
        rowEl.querySelector(".value")!.appendChild(cliEl);
    }
}
