import { formatSizeUnits, removeDuplicateUidsRanges, secondsToDhms } from "../../help";
import { StateData } from "../../data/types";
import { ERROR_DECR_BY_CODE_MAP } from "../../data/error-desc";
import { setNewValue } from ".";

let isTcpTemplatesSet = false;

enum TCPStatus {
    Trying = "Trying to access interface",
    Connected = "Connected",
}

export function updateTCPServer(data: StateData): void {
    if (!isTcpTemplatesSet) {
        setTemplates(data);
        isTcpTemplatesSet = true;
    }
    updateRs485Items(data);
}

function setTemplates(data: StateData): void {
    const listEl = document.querySelector("#tcp-list");
    const templateEl = document.querySelector("#tcp-table-template")!;

    if (!templateEl) {
        return;
    }

    for (let i = 0; i <= 3; i++) {
        const tmpClone = templateEl.cloneNode(true) as HTMLElement;
        const enabled = data[`mbtcpCli${i}Enabled`] as number;
        if (!enabled) {
            // tmpClone.classList.add("d-none");
        }

        tmpClone.setAttribute("id", `tcp-server-table-${i}`);
        tmpClone.querySelector(".tcp-ordinal-number")!.innerHTML = `#${i + 1}`;

        listEl?.appendChild(tmpClone);
    }

    templateEl?.remove();
}

function updateRs485Items(data: StateData): void {
    for (let i = 0; i <= 3; i++) {
        const el = document.querySelector(`#tcp-server-table-${i}`) as HTMLElement;
        if (!el) {
            continue;
        }
        updateTcpServItem(
            el,
            data[`mbtcpCli${i}Enabled`] as number,
            data[`mbtcpCli_${i}RxAvg`] as number,
            data[`mbtcpCli_${i}RxTotal`] as number,
            data[`mbtcpCli_${i}TxAvg`] as number,
            data[`mbtcpCli_${i}TxTotal`] as number,
            data[`mbtcpCli_${i}Netif`] as number,
            data[`mbtcpCli${i}ConnAttempts`] as number,
            data[`mbtcpCli${i}ConnTimestamp`] as number,
            data[`mbtcpCli${i}LastErrorCode`] as number,
            data[`mbtcpCli${i}Route`] as string,
            data.osCurTime,
        );
    }
}

function updateTcpServItem(
    el: HTMLElement,
    enabled: number,
    rxAvg: number,
    rxTotal: number,
    txAvg: number,
    txTotal: number,
    netif?: number,
    attempts?: number,
    connTime?: number,
    errorCode?: number,
    route?: string,
    osCurrentTime?: number,
): void {
    if (!enabled) {
        el.classList.add("d-none");
        return;
    }
    el.classList.remove("d-none");

    updateTcpServStatus(el.querySelector(".tcp-server-status") as HTMLElement, netif, attempts);
    updateTcpServLastConnection(
        el.querySelector(".tcp-server-last-connection") as HTMLElement,
        netif,
        connTime,
        osCurrentTime,
    );
    updateTcpServConnectionSince(
        el.querySelector(".tcp-server-connection-since") as HTMLElement,
        netif,
        connTime,
        osCurrentTime,
    );
    updateTcpServConnectionError(
        el.querySelector(".tcp-server-connection-error") as HTMLElement,
        netif,
        errorCode,
    );

    updateTcpSrvInterface(el.querySelector(".tcp-server-interface") as HTMLElement, netif);

    updateTcpServAddressRange(
        el.querySelector(".tcp-server-address-range") as HTMLElement,
        netif,
        route,
    );
    updateTcpSrvRxTx(
        el.querySelector(".tcp-server-rx-tx") as HTMLElement,
        rxAvg,
        rxTotal,
        txAvg,
        txTotal,
    );
}

function updateTcpServStatus(el: HTMLElement, netif?: number, attempts?: number): void {
    let status: string = TCPStatus.Connected;
    if (!netif) {
        status = `${TCPStatus.Trying} (attempt ${attempts || 0})`;
    }
    setNewValue(el.querySelector(".value")!, status);
}

function updateTcpServLastConnection(
    el: HTMLElement,
    netif?: number,
    connTime?: number,
    osCurrentTime?: number,
): void {
    if (netif || !connTime) {
        el.classList.add("d-none");
        return;
    }
    el.classList.remove("d-none");

    const sec = connTime
        ? (new Date(osCurrentTime! * 1000).getTime() - new Date(connTime * 1000).getTime()) / 1000
        : 0;
    el.classList.remove("d-none");
    el.querySelector(".value")!.innerHTML = secondsToDhms(sec);
}

function updateTcpServConnectionSince(
    el: HTMLElement,
    netif?: number,
    connTime?: number,
    osCurrentTime?: number,
): void {
    if (!netif || !connTime) {
        el.classList.add("d-none");
        return;
    }
    el.classList.remove("d-none");

    const sec = connTime
        ? (new Date(osCurrentTime! * 1000).getTime() - new Date(connTime * 1000).getTime()) / 1000
        : 0;
    el.classList.remove("d-none");
    el.querySelector(".value")!.innerHTML = secondsToDhms(sec);
}

function updateTcpServConnectionError(el: HTMLElement, netif?: number, errorCode?: number): void {
    if (netif || !errorCode) {
        el.classList.add("d-none");
        return;
    }

    const errorDesc = ERROR_DECR_BY_CODE_MAP.get(errorCode);
    if (!errorDesc) {
        el.classList.add("d-none");
        return;
    }

    el.classList.remove("d-none");
    setNewValue(el.querySelector(".value")!, `${errorCode} (${errorDesc})`);
}

function updateTcpSrvInterface(el: HTMLElement, netif?: number): void {
    let int = "Disabled";
    switch (netif) {
        case 0:
            int = "Disabled";
            break;
        case 1:
            int = "Loopback";
            break;
        case 2:
            int = "ELAN (Ethernet)";
            break;
        case 3:
            int = "WLAN (Wi-Fi)";
            break;
        case 255:
            int = "Other";
            break;
        default:
            int = "Disabled";
            break;
    }

    setNewValue(el.querySelector(".value")!, int);
}

function updateTcpServAddressRange(el: HTMLElement, netif?: number, route?: string): void {
    if (!netif || !route) {
        el.classList.add("d-none");
        return;
    }
    el.classList.remove("d-none");
    setNewValue(el.querySelector(".value")!, removeDuplicateUidsRanges(route));
}

function updateTcpSrvRxTx(
    el: HTMLElement,
    tcpRxAvg: number,
    tcpRxTotal: number,
    tcpTxAvg: number,
    tcpTxTotal: number,
): void {
    const value = `RX ${formatSizeUnits(tcpRxAvg / 60)}/s (${formatSizeUnits(
        tcpRxTotal,
    )} total) |<wbr> TX ${formatSizeUnits(tcpTxAvg / 60)}/s (${formatSizeUnits(
        tcpTxTotal,
    )} total)`;
    setNewValue(el.querySelector(".value")!, value);
}
