import { StateData } from "../data/types";
import { InetStatus } from "./table/internet";

export function updateFooter(data: StateData): void {
    updateInternetStatus(data.inetState);
    updateRs485Interfaces(data);
    updateTcp(data);
    updateOvervisStatus(data.ovbState, data.wgState);
}

function updateInternetStatus(inetState: number): void {
    const el = document.querySelector("#footer-internet-status");
    if (!el) {
        return;
    }

    if (!inetState) {
        el.querySelector(".value")!.innerHTML = InetStatus.Offline;
        return;
    }
    el.querySelector(".value")!.innerHTML = InetStatus.Online;
}

enum OvervisFooterState {
    Disabled = "Disabled",
    Disconnected = "Disconnected",
    ReverseConnection = "Reverse Connection",
    VPNAccess = "VPN Access",
}

function updateOvervisStatus(ovbState: number, wgState: number): void {
    const el = document.querySelector("#footer-overvis-status");
    if (!el) {
        return;
    }

    if (!ovbState) {
        el.querySelector(".value")!.innerHTML = OvervisFooterState.Disabled;
    } else if (ovbState && wgState > 1) {
        el.querySelector(".value")!.innerHTML = OvervisFooterState.VPNAccess;
    } else if (ovbState === 1) {
        el.querySelector(".value")!.innerHTML = OvervisFooterState.Disconnected;
    } else if (ovbState === 2 || ovbState === 3) {
        el.querySelector(".value")!.innerHTML = OvervisFooterState.ReverseConnection;
    } else {
        el.querySelector(".value")!.innerHTML = OvervisFooterState.Disabled;
    }
}

function updateRs485Interfaces(data: StateData): void {
    const rsEl = document.querySelector("#footer-rs485-interfaces");
    if (!rsEl) {
        return;
    }

    const interfaces: string[] = [];
    for (let i = 0; i <= 3; i++) {
        const status = data[`rs485${i}Status`] as number;
        const devName = data[`rs485${i}DevName`] as string | undefined;
        if (!devName || !status || status === 1) {
            continue;
        }
        interfaces.push(devName);
    }

    if (interfaces.length < 1) {
        rsEl.querySelector(".value")!.innerHTML = "None";
        return;
    }
    rsEl.querySelector(".value")!.innerHTML = interfaces.join(", ");
}

function updateTcp(data: StateData): void {
    const el = document.querySelector("#footer-tcp-clients");
    if (!el) {
        return;
    }

    let serversCount = 0;
    for (let i = 0; i <= 3; i++) {
        if (data[`mbtcpCli${i}Enabled`] && data[`mbtcpCli_${i}Netif`]) {
            serversCount += 1;
        }
    }

    document
        .querySelector("#footer-tcp-clients")!
        .querySelector(
            ".value",
        )!.innerHTML = `Clients: ${data.mbsrvClients} / Servers: ${serversCount}`;
}
