import { ControlLoadError } from "../../errors";
import { loadListSer } from "../../data/load";
import { catchAsync, fillSelect } from "../../help";

export async function fillRs485NetifLists(): Promise<void> {
    const errorControl = new ControlLoadError();
    const list = await loadListSer(errorControl);

    const selectEls = Array.from(document.querySelectorAll("select.netifs-list"));
    for (const selectEl of selectEls) {
        fillSelect(
            selectEl.id,
            list.map((i) => i.devName),
        );
    }
}

export function rigModbusRs485Sections(): void {
    const sectionEls = Array.from(document.querySelectorAll(".modbus-rs485-settings"));
    for (const sectionEl of sectionEls) {
        sectionEl.querySelectorAll("select.netifs-list,select.modbus-mode").forEach((e) =>
            e.addEventListener("change", () => {
                showHideSections();
            }),
        );
        const updateBtn = sectionEl.querySelector(`.update`) as HTMLElement;
        updateBtn.addEventListener(
            "click",
            catchAsync(async () => {
                updateBtn.querySelector(".bi-arrow-clockwise")?.classList.add("d-none");
                updateBtn.querySelector(".spinner-border")?.classList.remove("d-none");
                await fillRs485NetifLists();
                updateBtn.querySelector(".spinner-border")?.classList.add("d-none");
                updateBtn.querySelector(".bi-arrow-clockwise")?.classList.remove("d-none");
            }),
        );
    }
    showHideSections();
}

function showHideSections(): void {
    const sectionEls = Array.from(document.querySelectorAll(".modbus-rs485-settings"));
    let previousEnabled = false;
    for (const [i, sectionEl] of sectionEls.entries()) {
        const interfaceSelectEl = sectionEl.querySelector(
            "select.netifs-list",
        ) as HTMLSelectElement;
        const interfaceType = interfaceSelectEl.value;
        const settingsEl = sectionEl.querySelector(".enabled-settings") as HTMLDivElement;

        const enabled = interfaceType !== "";
        if (!enabled) {
            settingsEl.classList.add("d-none");
        } else {
            settingsEl.classList.remove("d-none");
        }

        if (i === 0 || enabled || previousEnabled) {
            sectionEl.classList.remove("d-none");
        }
        previousEnabled = enabled;

        const modbusMode = sectionEl.querySelector("select.modbus-mode") as HTMLSelectElement;
        const masterSettingEls = sectionEl.querySelectorAll(".modbus-master-setting");
        if (modbusMode.value === "0" || modbusMode.value === "1") {
            masterSettingEls.forEach((e) => e.classList.remove("d-none"));
        } else {
            masterSettingEls.forEach((e) => e.classList.add("d-none"));
        }
    }
}
