import { isValidIp } from "../../help";
import JustValidate from "just-validate";
import { Rules } from "../../modbus/validation-constructor-form";

export function validateForm(form: HTMLFormElement): JustValidate {
    const validation = new JustValidate(form, {
        errorFieldCssClass: "is-invalid",
        focusInvalidField: true,
    });
    validation
        .addField("#device-name", [
            {
                rule: Rules.MaxLength,
                value: 64,
            },
            {
                validator: (value): boolean => {
                    if (typeof value !== "string") {
                        return value;
                    }
                    if (!value) {
                        return true;
                    }
                    const m = value.match(/([ 0-9a-zA-Z-])/g);
                    if (!m) {
                        return false;
                    }
                    return !(m.length < value.length);
                },
                errorMessage:
                    "Can contain only: letters from a to z, the digits from 0 to 9, and the hyphen (-).",
            },
        ])
        // Ethernet
        .addField("#ethernet-netif-name", [
            {
                rule: Rules.MaxLength,
                value: 20,
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#ethernet-ip", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidIp(value) : value,
                errorMessage: "Invalid IPv4 address.",
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#ethernet-mask", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidIp(value) : value,
                errorMessage: "Invalid ethernet mask.",
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#ethernet-getway", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidIp(value) : value,
                errorMessage: "Invalid IPv4 address.",
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#ethernet-dns-ip", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidIp(value) : value,
                errorMessage: "Invalid IPv4 address.",
            },
        ])
        .addField("#ethernet-second-dns-ip", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidIp(value) : value,
                errorMessage: "Invalid IPv4 address.",
            },
            {
                rule: Rules.Required,
            },
        ])
        // WIFI
        // TODO_FUTURE: check ssid password min max
        .addField("#wifi-mode", [{ validator: () => true }])
        .addField("#wifi-sta-ssid", [
            {
                validator: (value, fields): boolean => {
                    const wifiMode = fields["#wifi-mode"].elem.value;
                    return wifiMode !== "1" || value !== "";
                },
                errorMessage: "WiFi SSID can't be empty in client mode.",
            },
        ])
        .addField("#wifi-netif-name", [
            {
                rule: Rules.MaxLength,
                value: 20,
            },
        ])
        .addField("#wifi-ap-ssid", [
            {
                rule: Rules.MaxLength,
                value: 32,
            },
            {
                rule: Rules.MinLength,
                value: 2,
            },
        ])
        .addField("#wifi-custom-ip", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidIp(value) : value,
                errorMessage: "Invalid IPv4 address.",
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#wifi-custom-mask", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidIp(value) : value,
                errorMessage: "Invalid IPv4 address.",
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#wifi-custom-getway", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidIp(value) : value,
                errorMessage: "Invalid IPv4 address.",
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#wifi-dns-ip", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidIp(value) : value,
                errorMessage: "Invalid IPv4 address.",
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#wifi-second-dns-ip", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidIp(value) : value,
                errorMessage: "Invalid IPv4 address.",
            },
            {
                rule: Rules.Required,
            },
        ])
        // Overvis
        .addField("#overvis-remote-host-name", [
            {
                rule: Rules.MaxLength,
                value: 64,
            },
            {
                rule: Rules.Required,
            },
        ])
        // Modbus TCP
        .addField("#modbus-server-port", [
            {
                rule: Rules.MinNumber,
                value: 1,
            },
            {
                rule: Rules.MaxNumber,
                value: 65535,
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#modbus-server-res-timeout", [
            {
                rule: Rules.MinNumber,
                value: 1,
            },
            {
                rule: Rules.MaxNumber,
                value: 60000,
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#modbus-server-simultaneous", [
            {
                rule: Rules.MinNumber,
                value: 1,
            },
            {
                rule: Rules.MaxNumber,
                value: 20,
            },
            {
                rule: Rules.Required,
            },
        ])
        // Modbus getway
        .addField("#getway-device-id", [
            {
                rule: Rules.MinNumber,
                value: 0,
            },
            {
                rule: Rules.MaxNumber,
                value: 255,
            },
            {
                rule: Rules.Required,
            },
        ])
        // Modbus RS-485
        .addField("#rs485-0-device-name", [
            {
                rule: Rules.MaxLength,
                value: 20,
            },
        ])
        .addField("#rs485-0-response-time", [
            {
                rule: Rules.MinNumber,
                value: 1,
            },
            {
                rule: Rules.MaxNumber,
                value: 60000,
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#rs485-0-route-id", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidRouteUids(value) : value,
                errorMessage: "Invalid input. Please specify ranges. Example: 0-25,50-255",
            },
        ])
        .addField("#rs485-1-device-name", [
            {
                rule: Rules.MaxLength,
                value: 20,
            },
        ])
        .addField("#rs485-1-response-time", [
            {
                rule: Rules.MinNumber,
                value: 1,
            },
            {
                rule: Rules.MaxNumber,
                value: 60000,
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#rs485-1-route-id", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidRouteUids(value) : value,
                errorMessage: "Invalid input. Please specify ranges. Example: 0-25,50-255",
            },
        ])
        .addField("#rs485-2-device-name", [
            {
                rule: Rules.MaxLength,
                value: 20,
            },
        ])
        .addField("#rs485-2-response-time", [
            {
                rule: Rules.MinNumber,
                value: 1,
            },
            {
                rule: Rules.MaxNumber,
                value: 60000,
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#rs485-2-route-id", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidRouteUids(value) : value,
                errorMessage: "Invalid input. Please specify ranges. Example: 0-25,50-255",
            },
        ])
        .addField("#rs485-3-device-name", [
            {
                rule: Rules.MaxLength,
                value: 20,
            },
        ])
        .addField("#rs485-3-response-time", [
            {
                rule: Rules.MinNumber,
                value: 1,
            },
            {
                rule: Rules.MaxNumber,
                value: 60000,
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#rs485-3-route-id", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidRouteUids(value) : value,
                errorMessage: "Invalid input. Please specify ranges. Example: 0-25,50-255",
            },
        ])
        .addField("#modbus-client-0-route-uid", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidRouteUids(value) : value,
                errorMessage: "Invalid input. Please specify ranges. Example: 0-25,50-255",
            },
        ])
        .addField("#modbus-client-1-route-uid", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidRouteUids(value) : value,
                errorMessage: "Invalid input. Please specify ranges. Example: 0-25,50-255",
            },
        ])
        .addField("#modbus-client-2-route-uid", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidRouteUids(value) : value,
                errorMessage: "Invalid input. Please specify ranges. Example: 0-25,50-255",
            },
        ])
        .addField("#modbus-client-3-route-uid", [
            {
                validator: (value): boolean =>
                    typeof value === "string" ? isValidRouteUids(value) : value,
                errorMessage: "Invalid input. Please specify ranges. Example: 0-25,50-255",
            },
        ]);

    return validation;
}

function isValidRouteUids(value: string): boolean {
    const limits = value.split(",");
    if (limits.length < 1) {
        return false;
    }

    for (const limit of limits) {
        const valuesArrStr = limit.split("-");
        if (valuesArrStr.length !== 2) {
            return false;
        }
        const [low, high] = valuesArrStr.map(Number);
        if (
            Number.isNaN(low) ||
            Number.isNaN(high) ||
            high < low ||
            low < 0 ||
            low > 255 ||
            high > 255 ||
            high < 0
        ) {
            return false;
        }
    }

    return true;
}
