import { formatSizeUnits, getServerPathByServerName, secondsToDhms } from "../../help";
import { StateData } from "../../data/types";
import { ERROR_DECR_BY_CODE_MAP } from "../../data/error-desc";
import { setNewValue } from ".";

enum OvbStatus {
    Disabled = "Disabled",
    Trying = "Trying to establish connection",
    Unlinked = "Unlinked",
    Linked = "Linked",
}

enum OvbNetifName {
    Disabled = "Disabled",
    Loopback = "Loopback",
    Elan = "ELAN (Ethernet)",
    Wlan = "WLAN (Wi-Fi)",
    Other = "Other",
}

export function updateOvervis(data: StateData): void {
    updateOvStatus(data.ovbState, data.ovbActCode, data.ovbConnAttempts);
    updateLastConnection(data.ovbState, data.ovbConnTimestamp, data.osCurTime);
    updateConnectionSince(data.ovbState, data.ovbConnTimestamp, data.osCurTime);
    updateConnectionError(data.ovbState, data.ovbLastErrorCode);
    updateActivationCode(data.ovbState, data.ovbActCode, data.ovbCurHostname);
    updateOvervisUrl(data.ovbState, data.ovbActCode, data.ovbCurHostname, data.mac);
    updateOvervisNetif(data.ovbNetif);
    updateRxTx(data.ovbRxAvg, data.ovbRxTotal, data.ovbTxAvg, data.ovbTxTotal);
}

function updateOvStatus(ovbState: number, ovbActCode: number, ovbConnAttempts: number): void {
    const el = document.querySelector("#overvis-status");
    if (!el) {
        return;
    }

    let status: string = OvbStatus.Unlinked;
    if (!ovbState) {
        status = OvbStatus.Disabled;
    } else if (ovbState === 1) {
        status = `${OvbStatus.Trying} <wbr />(attempt ${ovbConnAttempts})`;
    } else if (!ovbActCode && ovbState > 1) {
        status = OvbStatus.Linked;
    }

    setNewValue(el.querySelector(".value")!, status);
}

function updateLastConnection(
    ovbState: number,
    ovbConnTimestamp: number,
    osCurrentTime: number,
): void {
    const el = document.querySelector("#overvis-last-connection");
    if (!el) {
        return;
    }
    if (ovbState !== 1) {
        el.classList.add("d-none");
        return;
    }
    const sec = ovbConnTimestamp
        ? (new Date(osCurrentTime * 1000).getTime() -
              new Date(ovbConnTimestamp * 1000).getTime()) /
          1000
        : 0;
    el.classList.remove("d-none");
    setNewValue(el.querySelector(".value")!, secondsToDhms(sec));
}

function updateConnectionSince(
    ovbState: number,
    ovbConnTimestamp: number,
    osCurrentTime: number,
): void {
    const el = document.querySelector("#overvis-connection-since");
    if (!el) {
        return;
    }
    if (ovbState < 2) {
        el.classList.add("d-none");
        return;
    }

    const sec = osCurrentTime
        ? (new Date(osCurrentTime * 1000).getTime() -
              new Date(ovbConnTimestamp * 1000).getTime()) /
          1000
        : 0;

    el.classList.remove("d-none");
    setNewValue(el.querySelector(".value")!, secondsToDhms(sec));
}

function updateConnectionError(ovbState: number, ovbLastErrorCode: number): void {
    const el = document.querySelector("#overvis-connection-error");
    if (!el) {
        return;
    }
    const errorDecs = ERROR_DECR_BY_CODE_MAP.get(ovbLastErrorCode);
    if (ovbState !== 1) {
        el.classList.add("d-none");
        return;
    }
    setNewValue(
        el.querySelector(".value")!,
        `${ovbLastErrorCode} (${errorDecs || "No description."})`,
    );
}

function updateActivationCode(ovbState: number, ovbActCode: number, ovbCurHostname: string): void {
    const el = document.querySelector("#overvis-activation-code");
    if (!el) {
        return;
    }

    if (ovbState < 2 || !ovbActCode) {
        el.classList.add("d-none");
        return;
    }

    setNewValue(el.querySelector("#activation-code")!.querySelector(".value")!, `${ovbActCode}`);

    el.querySelector("#link-to-overvis-btn")!.setAttribute(
        "href",
        `${getServerPathByServerName(ovbCurHostname)}/login/?ac=${ovbActCode}`,
    );

    el.classList.remove("d-none");
}

function updateOvervisUrl(
    ovbState: number,
    ovbActCode: number,
    ovbCurHostname: string,
    _mac: string,
): void {
    const el = document.querySelector("#overvis-url");
    if (!el) {
        return;
    }
    if (ovbState < 2 || ovbActCode) {
        el.classList.add("d-none");
        return;
    }
    // https://ocp.overvis.com/manage/<wbr />network-by-mac/11:22:33:aa:bb:cc<
    const linkEl = document.querySelector("#link-to-overvis")!;
    const href = `${getServerPathByServerName(ovbCurHostname)}/manage/`;
    linkEl.setAttribute("href", href);
    linkEl.innerHTML = href;

    el.classList.remove("d-none");
}

function updateOvervisNetif(ovbNetif: number): void {
    const el = document.querySelector("#overvis-netif");
    if (!el) {
        return;
    }

    let netif = OvbNetifName.Disabled;
    switch (ovbNetif) {
        case 0:
            netif = OvbNetifName.Disabled;
            break;
        case 1:
            netif = OvbNetifName.Loopback;
            break;
        case 2:
            netif = OvbNetifName.Elan;
            break;
        case 3:
            netif = OvbNetifName.Wlan;
            break;
        case 255:
            netif = OvbNetifName.Other;
            break;
        default:
            break;
    }

    setNewValue(el.querySelector(".value")!, netif);
}

function updateRxTx(
    ovbRxAvg: number,
    ovbRxTotal: number,
    ovbTxAvg: number,
    ovbTxTotal: number,
): void {
    const el = document.querySelector("#overvis-rx-tx");
    if (!el) {
        return;
    }

    const value = `RX ${formatSizeUnits(ovbRxAvg / 60)}/s (${formatSizeUnits(
        ovbRxTotal,
    )} total) |<wbr> TX ${formatSizeUnits(ovbTxAvg / 60)}/s (${formatSizeUnits(
        ovbTxTotal,
    )} total)`;
    setNewValue(el.querySelector(".value")!, value);
}
