import { showDisconnectError } from "./errors";

const GLOBAL_SPINNER_ID = "reboot-spinner";
const TIMER_ID = "reboot-timer";

function updateTimer(seconds: number = 0): void {
    const secondsEl = document
        .querySelector(`#${TIMER_ID}`)
        ?.querySelector("#seconds") as HTMLElement;
    secondsEl.innerHTML = `${seconds}`;
}

export function hideGlobalSpinner(): void {
    document.querySelector(`#${GLOBAL_SPINNER_ID}`)?.classList.add("d-none");
    document.querySelector(`#${TIMER_ID}`)?.classList.add("d-none");
}
export function showGlobalSpinner(showTimerForSeconds: number = 0): void {
    document.querySelector(".opcb-ui-namespace")!.classList.remove("d-none");
    if (showTimerForSeconds) {
        let seconds = showTimerForSeconds;
        updateTimer(seconds);
        const interval = window.setInterval(() => {
            if (seconds < 0) {
                clearInterval(interval);
                showDisconnectError();
                hideGlobalSpinner();
            }

            updateTimer(seconds);
            seconds--;
        }, 1000);
        document.querySelector(`#${TIMER_ID}`)?.classList.remove("d-none");
    }

    document.querySelector(`#${GLOBAL_SPINNER_ID}`)?.classList.remove("d-none");
}
