import { ModbusInterfaces, StateData, WifiModes } from "../data/types";

const STATE_IMG_CONTAINER_ID = "state-img-container";

export function updateStateImg(data: StateData): void {
    const stateContainer = document.querySelector(`#${STATE_IMG_CONTAINER_ID}`) as HTMLElement;
    if (!stateContainer) {
        return;
    }
    stateContainer.classList.remove("d-none");

    updateElan(stateContainer, data);
    updateWlan(stateContainer, data);
    updateInternet(stateContainer, data.inetState);
    updateRs485(stateContainer, data);
    updateTcpClient(stateContainer, data);
    updateTcpServs(stateContainer, data);
    updateOvervis(stateContainer, data);
}

function updateElan(stateContainer: HTMLElement, data: StateData): void {
    const elanContainer = stateContainer.querySelector("#elan")!;

    if (!data.elanEnabled) {
        elanContainer.classList.add("d-none");
        return;
    }
    elanContainer.classList.remove("d-none");

    const ip = data.elanIp;
    const ipEl = stateContainer.querySelector("#elan-ip")!.querySelector("text")!;
    ipEl.innerHTML = ip;

    if (!data.elanState || data.elanState === 1 || data.elanState === 2) {
        stateContainer.querySelector("#elan-broken-line")?.classList.remove("d-none");
        stateContainer.querySelector("#elan-connect-line")?.classList.add("d-none");
        return;
    }

    stateContainer.querySelector("#elan-broken-line")?.classList.add("d-none");
    stateContainer.querySelector("#elan-connect-line")?.classList.remove("d-none");
}

function updateWlan(stateContainer: HTMLElement, data: StateData): void {
    const wlanContainer = stateContainer.querySelector("#wifi")!;
    if (!data.wlanEnabled) {
        wlanContainer.classList.add("d-none");
        return;
    }
    wlanContainer.classList.remove("d-none");

    const ipEl = stateContainer.querySelector("#wifi-ip")!;
    ipEl.querySelector("text")!.innerHTML = data.wlanIp;

    const connectLine = stateContainer.querySelector("#wifi-connect-line")!;
    const brokenLine = stateContainer.querySelector("#wifi-broken-line")!;
    const accessPointEl = stateContainer.querySelector("#wifi-access-point")!;

    if (!data.wlanState) {
        accessPointEl.classList.add("d-none");
        connectLine.classList.add("d-none");
        brokenLine.classList.add("d-none");
        ipEl.classList.add("d-none");
        return;
    }
    ipEl.classList.remove("d-none");

    if (data.wlanMode === WifiModes.AccessPoint) {
        accessPointEl.classList.remove("d-none");
        connectLine.classList.add("d-none");
        brokenLine.classList.add("d-none");
        return;
    }

    if (!data.wlanState || data.wlanState === 1 || data.wlanState === 2) {
        accessPointEl.classList.add("d-none");
        connectLine.classList.add("d-none");
        brokenLine.classList.remove("d-none");
        return;
    }

    accessPointEl.classList.add("d-none");
    connectLine.classList.remove("d-none");
    brokenLine.classList.add("d-none");
}

function updateInternet(stateContainer: HTMLElement, inetState: number): void {
    const connectLine = stateContainer.querySelector("#internet-connected-line")!;
    const offlineLine = stateContainer.querySelector("#internet-disconnect-line")!;

    if (!inetState) {
        connectLine.classList.add("d-none");
        offlineLine.classList.remove("d-none");
        return;
    }
    connectLine.classList.remove("d-none");
    offlineLine.classList.add("d-none");
}

function updateRs485(stateContainer: HTMLElement, data: StateData): void {
    for (let i = 0; i < 4; i++) {
        const name = data[`rs485${i}DevName`] as string | undefined;
        const status = data[`rs485${i}Status`] as number;
        if (!name || !status) {
            stateContainer.querySelector(`#rs-485-${i + 1}`)!.classList.add("d-none");
            continue;
        }
        stateContainer.querySelector(`#rs-485-${i + 1}-name`)!.querySelector("text")!.innerHTML =
            name;

        const mode = data[`rs485${i}Mode`] as number;

        if (mode === 2 || mode === 3) {
            stateContainer.querySelector(`#rs-485-${i + 1}-client`)?.classList.add("d-none");
            stateContainer.querySelector(`#rs-485-${i + 1}-master`)?.classList.remove("d-none");
        } else {
            stateContainer.querySelector(`#rs-485-${i + 1}-client`)?.classList.remove("d-none");
            stateContainer.querySelector(`#rs-485-${i + 1}-master`)?.classList.add("d-none");

            stateContainer
                .querySelector(`#rs-485-${i + 1}-client-uids`)!
                .querySelector("text")!.innerHTML = data[`rs485${i}Route`] as string;

            if (status === 1) {
                stateContainer
                    .querySelector(`#rs-485-${i + 1}-connect-line`)!
                    .classList.add("d-none");
                stateContainer
                    .querySelector(`#rs-485-${i + 1}-disconnect-line`)!
                    .classList.remove("d-none");
            } else {
                stateContainer
                    .querySelector(`#rs-485-${i + 1}-connect-line`)!
                    .classList.remove("d-none");
                stateContainer
                    .querySelector(`#rs-485-${i + 1}-disconnect-line`)!
                    .classList.add("d-none");
            }
        }

        stateContainer.querySelector(`#rs-485-${i + 1}`)!.classList.remove("d-none");
    }
}

function updateTcpClient(stateContainer: HTMLElement, data: StateData): void {
    const tcpCliEl = stateContainer.querySelector("#tcp-clients");
    if (!data.mbtcpSrvEnabled) {
        tcpCliEl?.classList.add("d-none");
        return;
    }
    tcpCliEl?.classList.remove("d-none");
    // set clients count
    stateContainer.querySelector("#tcp-clients-count")!.querySelector("text")!.innerHTML =
        data.mbsrvClients ? `${data.mbsrvClients}` : "No";
    if (data.mbsrvClients > 0) {
        stateContainer.querySelector(`#tcp-clients-connected`)?.classList.remove("d-none");
        stateContainer.querySelector(`#tcp-clients-empty`)?.classList.add("d-none");
    } else {
        stateContainer.querySelector(`#tcp-clients-connected`)?.classList.add("d-none");
        stateContainer.querySelector(`#tcp-clients-empty`)?.classList.remove("d-none");
    }
}

function updateTcpServs(stateContainer: HTMLElement, data: StateData): void {
    for (let i = 0; i < 4; i++) {
        const mode = data[`mbtcpCli${i}Enabled`] as ModbusInterfaces | undefined;
        const servEl = stateContainer.querySelector(`#tcp-server-${i + 1}`);
        if (!mode) {
            servEl?.classList.add("d-none");
            continue;
        }
        servEl?.classList.remove("d-none");

        const netif = data[`mbtcpCli_${i}Netif`] as number | undefined;
        if (!netif) {
            stateContainer
                .querySelector(`#tcp-server-${i + 1}-connected-line`)
                ?.classList.add("d-none");
            stateContainer
                .querySelector(`#tcp-server-${i + 1}-broken-line`)
                ?.classList.remove("d-none");
        } else {
            stateContainer
                .querySelector(`#tcp-server-${i + 1}-connected-line`)
                ?.classList.remove("d-none");
            stateContainer
                .querySelector(`#tcp-server-${i + 1}-broken-line`)
                ?.classList.add("d-none");
        }

        stateContainer
            .querySelector(`#tcp-server-${i + 1}-uids`)!
            .querySelector("text")!.innerHTML = (data[`mbtcpCli${i}Route`] as string) || "-";
    }
}

function updateOvervis(stateContainer: HTMLElement, data: StateData): void {
    const overvisEl = stateContainer.querySelector("#overvis")!;
    const state = data.ovbState;
    if (state === 0) {
        overvisEl.classList.add("d-none");
        return;
    }
    overvisEl.classList.remove("d-none");

    if (state !== 3) {
        stateContainer.querySelector("#overvis-connected")?.classList.add("d-none");
        stateContainer.querySelector("#overvis-connection-broken")?.classList.remove("d-none");
        return;
    }
    stateContainer.querySelector("#overvis-connected")?.classList.remove("d-none");
    stateContainer.querySelector("#overvis-connection-broken")?.classList.add("d-none");
}
