import { formatSizeUnits, removeDuplicateUidsRanges } from "../../help";
import { StateData } from "../../data/types";
import { ERROR_DECR_BY_CODE_MAP } from "../../data/error-desc";
import { setNewValue } from ".";

let isTemplatesSet = false;

enum Rs485Status {
    Trying = "Trying to access interface",
    RtuMaster = "RTU master",
    RtuSlave = "RTU slave",
    AsciiMaster = "ASCII master",
    AsciiSlave = "ASCII slave",
}

export function updateRs485(data: StateData): void {
    if (!isTemplatesSet) {
        setTemplates(data);
        isTemplatesSet = true;
    }
    updateRs485Items(data);
}

function setTemplates(data: StateData): void {
    const listEl = document.querySelector("#rs-485-list");
    const templateEl = document.querySelector("#rs-485-table-template")!;

    if (!templateEl) {
        return;
    }

    for (let i = 0; i <= 3; i++) {
        const devName = data[`rs485${i}DevName`] as string | undefined;
        const tmpClone = templateEl.cloneNode(true) as HTMLElement;
        if (!devName) {
            tmpClone.classList.add("d-none");
        }

        tmpClone.setAttribute("id", `rs-485-table-${i}`);
        tmpClone.querySelector(".interface-name")!.innerHTML = devName || "";
        tmpClone.querySelector(".rs-ordinal-number")!.innerHTML = `#${i + 1}`;

        listEl?.appendChild(tmpClone);
    }

    templateEl?.remove();
}

function updateRs485Items(data: StateData): void {
    for (let i = 0; i <= 3; i++) {
        const el = document.querySelector(`#rs-485-table-${i}`);
        if (!el) {
            continue;
        }
        const devName = data[`rs485${i}DevName`] as string;
        updateRsItem(
            el as HTMLElement,
            data[`rs485${i}Status`] as number,
            data[`rs485${i}RxAvg`] as number,
            data[`rs485${i}RxTotal`] as number,
            data[`rs485${i}TxAvg`] as number,
            data[`rs485${i}TxTotal`] as number,
            data[`rs485${i}Mode`] as number,
            devName,
            data[`rs485${i}Attempt`] as number,
            data[`rs485${i}Error`] as number,
            data[`rs485${i}Route`] as string,
        );
    }
}

function updateRsItem(
    el: HTMLElement,
    status: number,
    rs485RxAvg: number,
    rs485RxTotal: number,
    rs485TxAvg: number,
    rs485TxTotal: number,
    rs4850Mode: number,
    devName?: string,
    attempts?: number,
    error?: number,
    route?: string,
): void {
    if (!devName || !status) {
        el.classList.add("d-none");
        return;
    }
    el.classList.remove("d-none");
    el.querySelector(".interface-name")!.innerHTML = devName;

    updateRsStatus(el.querySelector(".rs-port-status")!, status, rs4850Mode, attempts);
    updateRsError(el.querySelector(".rs-port-error-code")!, status, error);
    updateRsAddressRange(el.querySelector(".rs-port-address-range")!, status, route);
    updateRsRxTx(
        el.querySelector(".rs-port-rx-tx")!,
        rs485RxAvg,
        rs485RxTotal,
        rs485TxAvg,
        rs485TxTotal,
    );
}

function updateRsStatus(el: HTMLElement, status: number, mode: number, attempts?: number): void {
    if (status === 1) {
        setNewValue(
            el.querySelector(".value")!,
            `${Rs485Status.Trying} (attempt ${attempts || 0})`,
        );
        return;
    }

    let statusStr = Rs485Status.RtuMaster;
    switch (mode) {
        case 0:
            statusStr = Rs485Status.RtuMaster;
            break;
        case 1:
            statusStr = Rs485Status.AsciiMaster;
            break;
        case 2:
            statusStr = Rs485Status.RtuSlave;
            break;
        case 3:
            statusStr = Rs485Status.AsciiSlave;
            break;
    }
    setNewValue(el.querySelector(".value")!, statusStr);
}

function updateRsError(el: HTMLElement, status: number, error?: number): void {
    if (status !== 1 || !error) {
        el.classList.add("d-none");
        return;
    }
    const errorDesc = ERROR_DECR_BY_CODE_MAP.get(error);
    if (!errorDesc) {
        el.classList.add("d-none");
        return;
    }

    el.classList.remove("d-none");
    setNewValue(el.querySelector(".value")!, `${error} (${errorDesc})`);
}

function updateRsAddressRange(el: HTMLElement, status: number, route?: string): void {
    if (status < 2 || !route) {
        el.classList.add("d-none");
        return;
    }

    el.classList.remove("d-none");
    setNewValue(el.querySelector(".value")!, removeDuplicateUidsRanges(route));
}

function updateRsRxTx(
    el: HTMLElement,
    rs485RxAvg: number,
    rs485RxTotal: number,
    rs485TxAvg: number,
    rs485TxTotal: number,
): void {
    const value = `RX ${formatSizeUnits(rs485RxAvg / 60)}/s (${formatSizeUnits(
        rs485RxTotal,
    )} total) |<wbr> TX ${formatSizeUnits(rs485TxAvg / 60)}/s (${formatSizeUnits(
        rs485TxTotal,
    )} total)`;

    setNewValue(el.querySelector(".value")!, value);
}
