import { loadSettingsData } from "../data/load";
import {
    fillEthernetNetifList,
    rigChangeEthernetDHCP,
    rigUpdateEthernetNetifList,
} from "./sectors/ethernet";
import {
    fillWifiISOSelect,
    fillWifiNetifList,
    fillWifiSsidNames,
    rigUpdateWifiNetifList,
    rigUpdateWifiSsidList,
    rigWifiChanges,
} from "./sectors/wifi";
import { rigSubmitForm } from "./submit";
import { ControlLoadError, showLoadError } from "../errors";
import { fillRs485NetifLists, rigModbusRs485Sections } from "./sectors/rs-485";
import { rigSelectPlus } from "./select-plus";
import { SettingsData } from "../data/types";
import { catchAsync, rigChangeFields } from "../help";
import { rigModbusClientSections } from "./sectors/modbus-client";
import { rigOpcbModbusSettings } from "./sectors/modbus-opcb";

const FORM_ID = "opcb-form";

export async function initSettings(): Promise<void> {
    const form = document.getElementById(FORM_ID) as HTMLFormElement;
    if (!form) {
        return;
    }

    rigChangeEthernetDHCP();
    rigWifiChanges();
    rigSubmitForm(form);
    await fillFormFields(form);
    rigSelectPlus();
    rigChangeFields(form);
    rigUpdateWifiSsidList();
    rigUpdateWifiNetifList();
    rigUpdateEthernetNetifList();
    rigModbusClientSections();
    rigModbusRs485Sections();
    rigOpcbModbusSettings();
}

async function fillFormFields(form: HTMLFormElement): Promise<void> {
    fillWifiISOSelect();
    try {
        const errorControl = new ControlLoadError();
        const data = await loadSettingsData(errorControl);
        fillGeneralFields(form, data);
        catchAsync(async () => {
            await Promise.all([
                fillWifiNetifList(),
                fillEthernetNetifList(),
                fillRs485NetifLists(),
                fillWifiSsidNames(),
            ]);
        })();
    } catch (e) {
        const text = (e as Error).message;
        showLoadError(text);
    }
}

function fillGeneralFields(form: HTMLFormElement, data: SettingsData): void {
    const elems = Array.from(form.querySelectorAll("[data-holds]"));
    for (const el of elems) {
        const name = el.attributes.getNamedItem("data-holds")!.value;
        if (!data.hasOwnProperty(name)) {
            throw new Error(`Unknown field name: ${name}`);
        }
        const val = data[name] as string;
        if (el instanceof HTMLInputElement && el.type === "checkbox") {
            el.checked = !!data[name];
            el.dispatchEvent(new Event("input"));
            el.dispatchEvent(new Event("change"));
        } else if (el instanceof HTMLInputElement) {
            el.setAttribute("value", val);
            el.dispatchEvent(new Event("input"));
            el.dispatchEvent(new Event("change"));
        } else if (el instanceof HTMLSelectElement) {
            if (el.classList.contains("select-with-plus")) {
                const optionEl = document.createElement("option");
                optionEl.value = val;
                optionEl.text = val;
                optionEl.selected = true;
                el.appendChild(optionEl);
            }
            el.value = val;
            el.dispatchEvent(new Event("change"));
        } else {
            el.innerHTML = val;
        }
    }
}
