export function rigOpcbModbusSettings(): void {
    const enabledCheckEl = document.querySelector("#getway-device-enable") as HTMLInputElement;
    enabledCheckEl.addEventListener("change", () => {
        showHideAdditionalSettings();
    });
    showHideAdditionalSettings();
}

function showHideAdditionalSettings(): void {
    const enabledCheckEl = document.querySelector("#getway-device-enable") as HTMLInputElement;
    const settingsEl = document.querySelector("#modbus-getway-section .enabled-settings");
    if (enabledCheckEl.checked) {
        settingsEl?.classList.remove("d-none");
    } else {
        settingsEl?.classList.add("d-none");
    }
}
