import { StateData } from "../../data/types";
import { updateElanTable } from "./elan";
import { updateGeneralTable } from "./general";
import { updateInternet } from "./internet";
import { updateOvervis } from "./overvis";
import { updateRs485 } from "./rs-485";
import { updateTcpClients } from "./tcp-clients";
import { updateTCPServer } from "./tcp-server";
import { updateWiFi } from "./wifi";
import { updateWireguard } from "./wireguard";

const STATE_ITEM_CLASS_NAME = "state-item";

export function updateStateTable(data: StateData): void {
    const stateElems = Array.from(document.querySelectorAll(`.${STATE_ITEM_CLASS_NAME}`));

    for (const el of stateElems) {
        const dataName = el.getAttribute("data-value-name")!;
        setNewValue(el.querySelector(".value")!, data[dataName] as string);
    }

    updateGeneralTable(data);
    updateElanTable(data);
    updateWiFi(data);
    updateInternet(data);
    updateOvervis(data);
    updateWireguard(data);
    updateTcpClients(data);
    updateRs485(data);
    updateTCPServer(data);
}

export function setNewValue(valEl: HTMLElement, newValue: string): void {
    const oldValue = valEl.innerHTML;
    if (oldValue === newValue) {
        return;
    }
    valEl.innerHTML = newValue;
}
