import { submit } from "../data/submit";
import { snakeCaseNamesByCamelCaseMap } from "../data/types";
import { catchAsync, clearChangedStatusFromForm, isItProxy } from "../help";
import { hideGlobalSpinner, showGlobalSpinner } from "../spinner";
import { validateQuickSetupForm } from "./validation";

export function rigSubmitQuickSetupForm(form: HTMLFormElement): void {
    validateQuickSetupForm(form).onSuccess(
        catchAsync(async (e) => {
            e!.preventDefault();
            showGlobalSpinner();
            await saveSettings(form);
            clearChangedStatusFromForm(form, true);
            hideGlobalSpinner();
            if (isItProxy()) {
                return;
            }
            window.location.href = "/index.html";
        }),
    );
}

async function saveSettings(form: HTMLFormElement): Promise<void> {
    const connectionType = (
        form.querySelector("input[name='connection-type-radio-btns']:checked") as HTMLInputElement
    ).value as "ethernet" | "wifi";
    const ethernetMode = (
        form.querySelector("input[name='ethernet-ip-mode-radio']:checked") as HTMLInputElement
    ).value as "automatic" | "manual";
    const wifiMode = (
        form.querySelector("input[name='wifi-ip-mode-radio']:checked") as HTMLInputElement
    ).value as "automatic" | "manual";

    const paramsObj: Record<string, string> = {
        wlan_mode: "1",
    };

    if (connectionType === "ethernet") {
        paramsObj["elan_dhcp"] = ethernetMode === "automatic" ? "1" : "0";
    } else {
        paramsObj["wlan_sta_dhcp"] = wifiMode === "automatic" ? "1" : "0";
    }

    const fields = Array.from(form.querySelectorAll("[data-holds].changed"));
    const snakeCaseNamesMap = snakeCaseNamesByCamelCaseMap();

    for (const field of fields) {
        const name = field.attributes.getNamedItem("data-holds")!.value;
        const snakeCaseName = snakeCaseNamesMap.get(name);
        if (!snakeCaseName) {
            throw new Error(`Can't find field name in snake case map: ${name}`);
        }
        if (field instanceof HTMLInputElement && field.type === "checkbox") {
            paramsObj[snakeCaseName] = field.checked ? "1" : "0";
        } else if (field instanceof HTMLInputElement || field instanceof HTMLSelectElement) {
            paramsObj[snakeCaseName] = field.value;
        }
    }
    if (Object.entries(paramsObj).length < 1) {
        return;
    }

    await submit(`./api/set_conf.json`, paramsObj);
}
