const ALERTS_CONTAINER_ID = "alerts-container";
const REBOOT_DEVICE_ALERT_ID = "reboot-device-alert";
const SUCCESS_ALERT_ID = "success-alert";
const WARNING_ALERT_ID = "warning-alert";
const DANGER_ALERT_ID = "danger-alert";
const INFO_ALERT_ID = "info-alert";

export enum MessageKind {
    Danger = "danger",
    Success = "success",
    Warning = "warning",
    Info = "info",
}

export function showAfterUpdatePageMessage(text: string, kind: MessageKind): void {
    sessionStorage.setItem("pageMessageText", text);
    sessionStorage.setItem("pageMessageKind", kind);
}

export function checkSavedPageMessage(): void {
    const text = sessionStorage.getItem("pageMessageText");
    const kind = sessionStorage.getItem("pageMessageKind") as MessageKind | undefined;

    if (!text) {
        return;
    }

    sessionStorage.removeItem("pageMessageText");
    sessionStorage.removeItem("pageMessageKind");

    showPageMessage(text, kind);
}

export function showPageMessage(text: string, kind: MessageKind = MessageKind.Info): void {
    const alertEl = getAlertEl(kind);
    alertEl.innerHTML = text;
    alertEl.classList.remove("d-none");
}

function getAlertEl(kind: MessageKind): HTMLElement {
    const container = document.querySelector(`#${ALERTS_CONTAINER_ID}`) as HTMLElement;
    switch (kind) {
        case MessageKind.Danger:
            return container.querySelector(`#${DANGER_ALERT_ID}`) as HTMLElement;
        case MessageKind.Success:
            return container.querySelector(`#${SUCCESS_ALERT_ID}`) as HTMLElement;
        case MessageKind.Warning:
            return container.querySelector(`#${WARNING_ALERT_ID}`) as HTMLElement;
        default:
            return container.querySelector(`#${INFO_ALERT_ID}`) as HTMLElement;
    }
}

export function showRebootDeviceAlert(): void {
    document.querySelector(`#${REBOOT_DEVICE_ALERT_ID}`)?.classList.remove("d-none");
}

export function hideRebootDeviceAlert(): void {
    document.querySelector(`#${REBOOT_DEVICE_ALERT_ID}`)?.classList.add("d-none");
}

export function hideAllAlerts(): void {
    const alerts = Array.from(document.querySelectorAll(".alert"));
    for (const alert of alerts) {
        alert.classList.add("d-none");
    }
}
