export async function loadListNif(errorControl: ControlLoadError): Promise<NetifListData[]> {
    const resp = await load<ListNifResponse>(LIST_NIF_URL, REQ_TIMEOUT_MAX, errorControl);

    if ("error_descr" in resp) {
        throw new Error(`${resp.error_descr}`);
    }

    const data = resp.list_nif;
    return data.map((r) => ({
        type: r.type,
        devName: r.dev_name,
        mac: r.mac,
    }));
}

