export async function loadWifiSsidNames(_errorControl: ControlLoadError): Promise<WifiSsidList[]> {
    const controller = new AbortController();
    const timeoutId = setTimeout(() => {
        controller.abort();
        return [];
    }, 15000);

    const resp = await fetch(LIST_WIFI_SSID_NAMES_URL, {
        signal: controller.signal,
    });
    clearTimeout(timeoutId);

    if (resp.status === 502) {
        throw new Error("The api of the device does not respond.");
    }

    try {
        const result = JSON.parse(await resp.text()) as WifiSsidResponse;

        if ("error_descr" in result) {
            return [];
        }
        return result.list_ap;
    } catch (e) {
        return [];
    }
}

async function load<T>(url: string, timeout: number, errorControl: ControlLoadError): Promise<T> {
    const controller = new AbortController();
    const timeoutId = setTimeout(() => {
        controller.abort();
        errorControl.showError("disconnect");
    }, timeout);

    const resp = await fetch(url, { signal: controller.signal });
    clearTimeout(timeoutId);

    if (resp.status === 502) {
        throw new Error("The api of the device does not respond.");
    }

    errorControl.hideError();

    const body = await resp.text();
    if (!body) {
        throw new Error("Empty result.");
    }

    return JSON.parse(body) as T;
}
