import { MessageKind, showAfterUpdatePageMessage } from "../alert";
import { getCookieValue } from "../help";

export async function submit(
    url: string,
    data?: Record<string, string>,
): Promise<{ status: "ok"; res: Response } | { status: "err" }> {
    const csrf = getCookieValue("_csrf");
    if (csrf && data) {
        data._csrf = csrf;
    }

    const res = await fetch(url, {
        method: "POST",
        headers: {
            "Content-Type": "application/x-www-form-urlencoded",
        },
        body: data ? new URLSearchParams(data) : null,
    });

    if (res.status === 401) {
        alert("Your account is not authorized to make this request.");
        return {
            status: "err",
        };
    }

    if (res.status !== 200) {
        alert(`Submit error. Please try again.`);
        return {
            status: "err",
        };
    }

    showAfterUpdatePageMessage("Settings saved and applied.", MessageKind.Success);
    return {
        status: "ok",
        res,
    };
}
