import { Tab } from "bootstrap";

export function initTabPanel(): void {
    if (!document.getElementById(`settings-nav-menu`)) {
        return;
    }
    rigMenu();
    loadLastTab();
}

function rigMenu(): void {
    function getParentButton(btn: Element): Element {
        const parentNavItem = btn.closest(".nav-item");
        if (!parentNavItem) {
            throw new Error("can't find dropdown parent nav-item");
        }
        const button = parentNavItem.querySelector("button.nav-link");
        if (!button) {
            throw new Error("can't find dropdown parent button");
        }
        return button;
    }

    const container = document.querySelector(`#settings-nav-menu`) as HTMLElement;

    // rigging dropdown menu links to tabs
    const btns = Array.from(container.querySelectorAll(".dropdown-item[data-bs-target]"));
    for (const btn of btns) {
        btn.addEventListener("show.bs.tab", () => {
            getParentButton(btn).classList.add("active");
        });
        btn.addEventListener("hide.bs.tab", () => {
            getParentButton(btn).classList.remove("active");
        });
    }

    // saving selected tab to the session
    const tabs = Array.from(container.querySelectorAll("[data-bs-target]"));
    for (const tab of tabs) {
        tab.addEventListener("shown.bs.tab", () => {
            const tabName = tab.getAttribute("data-bs-target")!;
            sessionStorage.setItem("tabName", tabName);
        });
    }
}

function loadLastTab(): void {
    const tabName = sessionStorage.getItem("tabName");
    if (!tabName) {
        return;
    }
    selectTabById(tabName);
}

export function selectTabById(id: string): void {
    const container = document.querySelector(`#settings-nav-menu`) as HTMLElement | undefined;
    if (!container) {
        return;
    }

    const tab = container.querySelector(`[data-bs-target="${id}"]`) as HTMLElement | undefined;
    if (!tab) {
        return;
    }

    const tabInstance = Tab.getOrCreateInstance(tab);
    if (!tabInstance) {
        return;
    }

    tabInstance.show();
}
