import { catchAsync, isItProxy, rigChangeFields } from "../help";
import { DHCPModes, QuickSetupSettingsData, WifiModes, WifiSsidList } from "../data/types";
import { rigSelectPlus } from "../settings/select-plus";
import { ControlLoadError, showLoadError } from "../errors";
import {
    loadDeviceModelName,
    loadDeviceUserDefinedName,
    loadQuickSetupStateData,
    loadQuickSetupSettingsData,
    loadWifiSsidNames,
    loadStaticData,
} from "../data/load";
import { MessageKind, showPageMessage } from "../alert";
import { rigSubmitQuickSetupForm } from "./submit";
import { setDeviceName, setFwVersion } from "../state/static";

export async function initQuickSetup(): Promise<void> {
    const container = document.querySelector(`#opcb-quick-setup-container`) as
        | HTMLElement
        | undefined;
    if (!container) {
        return;
    }
    const form = container.querySelector("form") as HTMLFormElement;
    const errorControl = new ControlLoadError();

    const settingsData = await loadQuickSetupSettingsData(errorControl);
    const state = await loadQuickSetupStateData(errorControl);
    const staticData = await loadStaticData(errorControl);
    const userDefinedName = await loadDeviceUserDefinedName(errorControl);
    const modelName = await loadDeviceModelName(errorControl);

    rigChangeConnectionType(container);
    rigChangeEthernetDHCP(container);
    rigChangeWifiIpMode(container);

    fillFormFields(settingsData, form, container, errorControl);

    rigSelectPlus();
    rigChangeFields(form);
    rigUpdateWifiSsidList(errorControl);
    rigSubmitQuickSetupForm(form);

    setDeviceName(modelName, userDefinedName);
    setPageMessages(state.inetState, state.wlanMode);
    setConnectionType(
        state.elanEnabled,
        state.wlanMode,
        state.wlanState,
        state.wlanEnabled,
        state.elanState,
    );
    setFwVersion(staticData.ver, staticData.verCommit);

    if (isItProxy()) {
        document.querySelector("header")?.classList.add("d-none");
        document.querySelector("#quick-setup-skip-btn")?.classList.add("d-none");
        document.querySelector("#quick-satart-save-btn")?.classList.remove("btn-primary");
        document.querySelector("#quick-satart-save-btn")?.classList.add("unmain-submit-btn");
        document.querySelector("#quick-satart-save-btn")?.setAttribute("disabled", "disabled");
    }
}

function setPageMessages(inetState: number, wlanModes: WifiModes): void {
    if (!inetState) {
        return;
    }

    let internetMessageText = "Device has internet access by the Ethernet link.";
    if (wlanModes === WifiModes.AccessPoint) {
        internetMessageText += " Submit this form to disable the WiFi Access Point.";
        document.querySelector("#off-access-point-text")?.classList.remove("d-none");
    }

    showPageMessage(internetMessageText, MessageKind.Success);
}

function setConnectionType(
    elanEnabled: number,
    wlanMode: number,
    wlanState: number,
    wlanEnabled: number,
    elanState: number,
): void {
    if (!((!elanEnabled || elanState < 3) && wlanEnabled && wlanState > 2 && wlanMode === 1)) {
        return;
    }

    const wifiEl = (document.querySelector("#wifi-connection-type") as HTMLInputElement)!;
    wifiEl.checked = true;
    (document.querySelector("#quick-start-internet-methods") as HTMLElement).dispatchEvent(
        new Event("change"),
    );
}

function rigChangeConnectionType(container: HTMLElement): void {
    const internetOptionsContainer = container.querySelector(
        "#quick-start-internet-methods",
    ) as HTMLElement;

    internetOptionsContainer.addEventListener("change", () => {
        const activeOption = (
            internetOptionsContainer.querySelector(
                "input[name='connection-type-radio-btns']:checked",
            ) as HTMLInputElement
        ).value;
        container.classList.remove("ethernet", "wifi");
        container.classList.add(activeOption);
    });
}

function rigChangeEthernetDHCP(container: HTMLElement): void {
    const radioBtnsContainer = container.querySelector(
        `#ethernet-ip-mode-container`,
    ) as HTMLSelectElement;

    const customSettEl = container.querySelector(`#ethernet-custom-settings`);

    radioBtnsContainer.addEventListener("change", () => {
        const value =
            (
                container.querySelector(
                    "input[name='ethernet-ip-mode-radio']:checked",
                ) as HTMLInputElement
            ).value === "automatic"
                ? DHCPModes.Automatic
                : DHCPModes.Custom;

        if (value === DHCPModes.Automatic) {
            customSettEl?.classList.add("d-none");
            return;
        }
        customSettEl?.classList.remove("d-none");
    });
}

function rigChangeWifiIpMode(container: HTMLElement): void {
    const radioBtnsContainer = container.querySelector(
        `#wifi-ip-mode-container`,
    ) as HTMLSelectElement;

    const customSettEl = container.querySelector(`#wifi-dhcp-custom-sett`);

    radioBtnsContainer.addEventListener("change", () => {
        const value =
            (
                container.querySelector(
                    "input[name='wifi-ip-mode-radio']:checked",
                ) as HTMLInputElement
            ).value === "automatic"
                ? DHCPModes.Automatic
                : DHCPModes.Custom;

        if (value === DHCPModes.Automatic) {
            customSettEl?.classList.add("d-none");
            return;
        }
        customSettEl?.classList.remove("d-none");
    });
}

function rigUpdateWifiSsidList(errorControl: ControlLoadError): void {
    const updateBtn = document.querySelector(`#wifi-ssid-update-btn`) as HTMLElement;

    updateBtn.addEventListener(
        "click",
        // eslint-disable-next-line @typescript-eslint/no-misused-promises
        async () => {
            updateBtn.setAttribute("disabled", "disabled");
            updateBtn.querySelector(".bi-arrow-clockwise")?.classList.add("d-none");
            updateBtn.querySelector(".spinner-border")?.classList.remove("d-none");
            await fillWifiSsidNames(errorControl);
            updateBtn.removeAttribute("disabled");
        },
    );
}

function setEthernetIpMode(container: HTMLElement, mode: DHCPModes): void {
    const radioBtnsContainer = container.querySelector(
        `#ethernet-ip-mode-container`,
    ) as HTMLSelectElement;

    if (mode === DHCPModes.Automatic) {
        (radioBtnsContainer.querySelector("#etherbet-auto-radio") as HTMLInputElement).checked =
            true;
        radioBtnsContainer.dispatchEvent(new Event("change"));
        return;
    }
    (radioBtnsContainer.querySelector("#ethernet-manual-radio") as HTMLInputElement).checked =
        true;
    radioBtnsContainer.dispatchEvent(new Event("change"));
}

function setWifiIpMode(container: HTMLElement, mode: DHCPModes): void {
    const radioBtnsContainer = container.querySelector(
        `#wifi-ip-mode-container`,
    ) as HTMLSelectElement;

    if (mode === DHCPModes.Automatic) {
        (radioBtnsContainer.querySelector("#wifi-auto-radio") as HTMLInputElement).checked = true;
        radioBtnsContainer.dispatchEvent(new Event("change"));
        return;
    }
    (radioBtnsContainer.querySelector("#wifi-manual-radio") as HTMLInputElement).checked = true;
    radioBtnsContainer.dispatchEvent(new Event("change"));
}

function fillFormFields(
    data: QuickSetupSettingsData,
    form: HTMLFormElement,
    container: HTMLElement,
    errorControl: ControlLoadError,
): void {
    try {
        fillGeneralFields(form, data);
        // eslint-disable-next-line @typescript-eslint/no-floating-promises
        catchAsync(async () => {
            await Promise.all([fillWifiSsidNames(errorControl)]);
        })();
        setEthernetIpMode(container, data.elanDhcp);
        setWifiIpMode(container, data.wlanStaDhcp);
    } catch (e) {
        const text = (e as Error).message;
        showLoadError(text);
    }
}

function fillGeneralFields(form: HTMLFormElement, data: QuickSetupSettingsData): void {
    const elems = Array.from(form.querySelectorAll("[data-holds]"));
    for (const el of elems) {
        const name = el.attributes.getNamedItem("data-holds")!.value;
        if (!data.hasOwnProperty(name)) {
            throw new Error(`Unknown field name: ${name}`);
        }
        const val = data[name] as string;
        if (el instanceof HTMLInputElement && el.type === "checkbox") {
            el.checked = !!data[name];
            el.dispatchEvent(new Event("input"));
            el.dispatchEvent(new Event("change"));
        } else if (el instanceof HTMLInputElement) {
            el.setAttribute("value", val);
            el.dispatchEvent(new Event("input"));
            el.dispatchEvent(new Event("change"));
        } else if (el instanceof HTMLSelectElement) {
            if (el.classList.contains("select-with-plus")) {
                const optionEl = document.createElement("option");
                optionEl.value = val;
                optionEl.text = val;
                optionEl.selected = true;
                el.appendChild(optionEl);
            }
            el.value = val;
            el.dispatchEvent(new Event("change"));
        } else {
            el.innerHTML = val;
        }
    }
}

async function fillWifiSsidNames(errorControl: ControlLoadError): Promise<void> {
    const updateBtn = document.querySelector(`#wifi-ssid-update-btn`) as HTMLElement;
    let list: WifiSsidList[];
    try {
        list = await loadWifiSsidNames(errorControl);
    } catch (err) {
        updateBtn.querySelector(".bi-arrow-clockwise")?.classList.remove("d-none");
        updateBtn.querySelector(".spinner-border")?.classList.add("d-none");
        return;
    }

    const sortedList = list.sort((a, b) => (a.signal < b.signal ? 1 : -1));

    fillWifiSsidSelect("wifi-sta-ssid", sortedList);
    updateBtn.querySelector(".bi-arrow-clockwise")?.classList.remove("d-none");
    updateBtn.querySelector(".spinner-border")?.classList.add("d-none");
}

function fillWifiSsidSelect(selectId: string, list: WifiSsidList[]): void {
    const listEl = document.querySelector(`#${selectId}`) as HTMLSelectElement;
    const currentSsid = listEl.value;

    listEl.innerHTML = "";

    const defaultOption = document.createElement("option");
    defaultOption.value = "";
    defaultOption.text = "----";
    listEl.appendChild(defaultOption);

    const addOption = (ssid: string, signal?: number): void => {
        const option = document.createElement("option");
        option.value = ssid;
        option.text = signal ? `${ssid} (${signal} dBm)` : ssid;
        option.selected = ssid === currentSsid;
        listEl.appendChild(option);
    };
    const ssids = list.map((l) => l.ssid);
    if (!ssids.includes(currentSsid)) {
        addOption(currentSsid);
    }

    for (const item of list) {
        addOption(item.ssid, item.signal);
    }
}
