import JustValidate from "just-validate";

// NOTE: Manually declaring Rules type as a workaround for the just-validate typings + parcel issue.
export enum Rules {
    Required = "required",
    Email = "email",
    MinLength = "minLength",
    MaxLength = "maxLength",
    Password = "password",
    Number = "number",
    MaxNumber = "maxNumber",
    MinNumber = "minNumber",
    StrongPassword = "strongPassword",
    CustomRegexp = "customRegexp",
    MinFilesCount = "minFilesCount",
    MaxFilesCount = "maxFilesCount",
    Files = "files",
}

export function validateConstructorForm(form: HTMLFormElement): JustValidate {
    const validation = new JustValidate(form, {
        errorFieldCssClass: "is-invalid",
        focusInvalidField: true,
    });

    validation
        .addField("#uid", [
            {
                rule: Rules.MaxNumber,
                value: 255,
            },
            {
                rule: Rules.MinNumber,
                value: 0,
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#register", [
            {
                rule: Rules.MaxNumber,
                value: 65535,
            },
            {
                rule: Rules.MinNumber,
                value: 0,
            },
            {
                rule: Rules.Required,
            },
        ])
        .addField("#quantity", [
            {
                rule: Rules.MinNumber,
                value: 1,
            },
        ]);

    return validation;
}
