import { showGlobalSpinner } from "../spinner";
import { catchAsync } from "../help";
import { submit } from "../data/submit";

export function rigDeactivateCode(): void {
    document.querySelector(`#unlink-btn`)?.addEventListener(
        "click",
        catchAsync(async () => {
            const conf = confirm("Confirm unlinking device from the Overvis account.");
            if (!conf) {
                return;
            }
            await deactivateCode();
        }),
    );
}

async function deactivateCode(): Promise<void> {
    showGlobalSpinner(60);
    sessionStorage.setItem("isReboot", "true");

    await submit("./api/cmd.json?cmd_ovb_deactivate");
}
