import { Rs485Modes } from "../data/types";
import { numberToHex16, numberToHex8 } from "../help";

export type ResponseData =
    | {
          response: string;
      }
    | {
          error_code: number;
          error_descr: string;
      };

export enum ModbusCommand {
    ReadCoils = "0x01",
    ReadDiscreteInputs = "0x02",
    ReadHoldingRegisters = "0x03",
    ReadInputRegisters = "0x04",
    WriteSingleCoil = "0x05",
    WriteSingleRegister = "0x06",
    WriteMultipleCoils = "0x0F",
    WriteMultipleRegisters = "0x10",
}

export enum RequestType {
    Opcb = "opcb",
    Mbtcp = "mbtcp",
    Rs485 = "rs485",
}

export function numberToHiLowBytes(value: number): [number, number] {
    return [(value >> 8) & 0xff, value & 0xff];
}

export function getCrc16(data: number[]): number {
    let crc = 0xffff;
    for (let b of data) {
        for (let i = 0; i < 8; i++) {
            const first8b = crc & 0xff;
            crc = (b ^ first8b) & 1 ? (crc >> 1) ^ 0xa001 : crc >> 1;
            b >>= 1;
        }
    }
    return crc;
}

export function getLrc8(data: number[]): number {
    let lrc = 0;
    for (const b of data) {
        let first8b = b & 0xff;
        lrc += first8b++;
    }
    return 0xff - lrc + 1;
}

export function formatTcpRequest(data: number[], lastTransId: number): string {
    const tid = numberToHiLowBytes(lastTransId);
    const pid = numberToHiLowBytes(0);
    data.unshift(...numberToHiLowBytes(data.length));
    data.unshift(...pid);
    data.unshift(...tid);
    return data.map(numberToHex8).join(":");
}

export function formatRtuRequest(data: number[]): string {
    const crc16Arr = numberToHex16(getCrc16(data)).split(":");
    const res = `${data.map(numberToHex8).join(":")}:${crc16Arr[1]}:${crc16Arr[0]}`;
    return res;
}

export function formatAsciiRequest(data: number[]): string {
    const hexData = Array.from(data.map(numberToHex8).join(""))
        .map((h) => h.toUpperCase().charCodeAt(0))
        .map(numberToHex8);

    const lrc = Array.from(numberToHex8(getLrc8(data)))
        .map((h) => h.toUpperCase().charCodeAt(0))
        .map(numberToHex8)
        .join(":");
    return `3a:${hexData.join(":")}:${lrc}:0d:0a`;
}

export function formatResponseBytesStr(response: string): string {
    const bytes: string[] = [];
    while (response.length > 0) {
        bytes.push(response.slice(0, 2));
        response = response.substring(2);
    }
    return bytes.join(":");
}

export function getRS485ModeName(val: Rs485Modes): string {
    if (val === 0) {
        return "Modbus RTU Master";
    }
    return "Modbus ASCII Master";
}
