import { ControlLoadError, showDisconnectError } from "../../errors";
import { snakeCaseNamesByCamelCaseMap } from "../../data/types";
import { validateForm } from "./validate";
import { catchAsync, isValidIp } from "../../help";
import { loadSettingsData } from "../../data/load";
import { hideGlobalSpinner, showGlobalSpinner } from "../../spinner";
import { selectTabById } from "../../tabpanel";
import { submit } from "../../data/submit";

export function rigSubmitForm(form: HTMLFormElement): void {
    validateForm(form)
        .onSuccess(
            catchAsync(async (e) => {
                e!.preventDefault();
                const errorControl = new ControlLoadError();
                const settings = await loadSettingsData(errorControl);
                const result = await saveSettings(form);
                if (result.status !== "ok") {
                    return;
                }

                if (isValidIp(window.location.hostname)) {
                    if (window.location.hostname === settings.wlanStaIp && result.wlanStaIp) {
                        redirectToNewHostProccess(
                            `http://${result.wlanStaIp}${window.location.pathname}`,
                        );
                        return;
                    }
                    if (result.elanIp) {
                        redirectToNewHostProccess(
                            `http://${result.elanIp}${window.location.pathname}`,
                        );
                        return;
                    }
                }

                window.location.reload();
            }),
        )
        .onFail((fields) => {
            for (const key in fields) {
                const fieldItem = fields[key];
                if (!fieldItem.isValid) {
                    const tabPaneId = fieldItem.elem.closest(".tab-pane")?.id;
                    if (!tabPaneId) {
                        break;
                    }
                    selectTabById(`#${tabPaneId}`);
                    break;
                }
            }
        });
}

async function saveSettings(
    form: HTMLFormElement,
): Promise<
    | { status: "ok"; wlanStaIp?: string; elanIp?: string }
    | { status: "no-data" }
    | { status: "err" }
> {
    const fields = Array.from(form.querySelectorAll("[data-holds].changed"));
    const snakeCaseNamesMap = snakeCaseNamesByCamelCaseMap();

    const paramsObj: Record<string, string> = {};

    for (const field of fields) {
        const name = field.attributes.getNamedItem("data-holds")!.value;
        const snakeCaseName = snakeCaseNamesMap.get(name);
        if (!snakeCaseName) {
            throw new Error(`Can't find field name in snake case map: ${name}`);
        }
        if (field instanceof HTMLInputElement && field.type === "checkbox") {
            paramsObj[snakeCaseName] = field.checked ? "1" : "0";
        } else if (field instanceof HTMLInputElement || field instanceof HTMLSelectElement) {
            paramsObj[snakeCaseName] = field.value;
        }
    }
    if (Object.entries(paramsObj).length < 1) {
        return { status: "no-data" };
    }

    const submitStatus = await submit(`./api/set_conf.json`, paramsObj);
    if (submitStatus.status === "err") {
        return { status: "err" };
    }

    return {
        status: "ok",
        wlanStaIp: paramsObj["wlan_sta_ip"],
        elanIp: paramsObj["elan_ip"],
    };
}

function redirectToNewHostProccess(newHostname: string): void {
    showGlobalSpinner(60);
    const interval = setInterval(async () => {
        const resp = await fetch("./api/get_sta.json");
        if (resp.status === 200) {
            clearInterval(interval);
            window.location.href = newHostname;
        }
    }, 1000);

    setTimeout(() => {
        if (interval) {
            hideGlobalSpinner();
            showDisconnectError();
            clearInterval(interval);
        }
    }, 60000);
}
