import * as moment from "moment";
import { ERROR_DECR_BY_CODE_MAP } from "../data/error-desc";
import { loadSettingsData, loadStateData } from "../data/load";
import { submit } from "../data/submit";
import { ModbusInterfaces, Rs485Modes, Rs485Status, SettingsData, StateData } from "../data/types";
import { ControlLoadError } from "../errors";
import { catchAsync, numberToHex8 } from "../help";
import { initRequestConstructor } from "./constructor";
import { formatResponseBytesStr, getRS485ModeName, RequestType, ResponseData } from "./data";

const logDateFormat = "YYYY-MM-DD HH:mm:ss";

export async function initModbus(): Promise<void> {
    const container = document.querySelector(`#modbus-container`) as HTMLElement;
    if (!container) {
        return;
    }
    const errorControl = new ControlLoadError();
    const settingsData = await loadSettingsData(errorControl);
    const stateData = await loadStateData(errorControl);

    initRequest(container, settingsData, stateData);
    initRequestConstructor(container, stateData);
}

function initRequest(
    container: HTMLElement,
    settingsData: SettingsData,
    stateData: StateData,
): void {
    container.querySelector(`#modbus-request`)?.addEventListener("change", () => saveHex());

    rigChangeInterfaceType(container, settingsData.virtualUid);
    rigClearLogBtn(container);
    rigSubmitForm(container, stateData);
    setInterfaceOptions(container, settingsData, stateData);

    loadHex();
}

function saveHex(): void {
    const container = document.querySelector(`#modbus-container`) as HTMLElement;
    if (!container) {
        return;
    }
    const selectEl = container.querySelector(`#interface-select`) as HTMLSelectElement;
    const requestsField = container.querySelector(`#modbus-request`) as HTMLTextAreaElement;
    const storageKey = `modbus-request-${selectEl.value}`;
    sessionStorage.setItem(storageKey, requestsField.value);
}

function loadHex(): void {
    const container = document.querySelector(`#modbus-container`) as HTMLElement;
    if (!container) {
        return;
    }
    const selectEl = container.querySelector(`#interface-select`) as HTMLSelectElement;
    const requestsField = container.querySelector(`#modbus-request`) as HTMLTextAreaElement;
    const storageKey = `modbus-request-${selectEl.value}`;
    requestsField.value = sessionStorage.getItem(storageKey) || "";
}

function rigChangeInterfaceType(container: HTMLElement, virtualUid: number): void {
    const selectEl = container.querySelector(`#interface-select`) as HTMLSelectElement;
    const requestsField = container.querySelector(`#modbus-request`) as HTMLTextAreaElement;
    selectEl.addEventListener("change", () => {
        sessionStorage.setItem(`modbus-interface`, selectEl.value);
        requestsField.value = "";
        if (selectEl.value === "opcb||self") {
            (container.querySelector("#uid") as HTMLInputElement).value = virtualUid.toString();
        }
        loadHex();
    });
}

function rigSubmitForm(container: HTMLElement, stateData: StateData): void {
    const form = container.querySelector(`#modbus-req-form`) as HTMLFormElement;
    form.addEventListener(
        "submit",
        catchAsync(async (e) => {
            e.preventDefault();
            await submitForm(form, stateData);
        }),
    );
}

function rigClearLogBtn(container: HTMLElement): void {
    const btn = container.querySelector(`#clear-log-btn`);
    btn?.addEventListener("click", () => {
        const el = container.querySelector(`#responses-list`) as HTMLTextAreaElement;
        el.innerHTML = "";
        container.querySelector(`#responses-container`)?.classList.add("d-none");
    });
}

async function submitForm(form: HTMLElement, stateData: StateData): Promise<void> {
    const spinner = form.querySelector("#sending-spinner")!;
    spinner.classList.remove("d-none");
    const submitEls = form.querySelector(`#submit-elements-container`)!;
    submitEls.classList.add("d-none");

    const requests = parseRequestsText(
        (form.querySelector(`#modbus-request`) as HTMLTextAreaElement).value,
        form,
    );
    const { path } = getInterfaceTypeAndPath(form, stateData);

    if (!requests || requests.length < 1) {
        spinner.classList.add("d-none");
        submitEls.classList.remove("d-none");
        return;
    }

    form.querySelector(`#responses-container`)?.classList.remove("d-none");

    for (const request of requests) {
        const requestApiStr = request.map(numberToHex8).join("");
        const requestStr = request.map(numberToHex8).join(":");
        logItem(requestStr, path, "request");
        const response = await submit(`./api/mbgate.json`, {
            request: requestApiStr,
            type: "1",
            dst: path,
        });

        if (response.status === "err") {
            spinner.classList.add("d-none");
            submitEls.classList.remove("d-none");
            return;
        }

        const result = JSON.parse(await response.res.text()) as ResponseData;
        if ("error_code" in result) {
            logItem(
                ERROR_DECR_BY_CODE_MAP.get(Number(result.error_code)) || result.error_descr,
                path,
                "response",
                true,
            );
        } else {
            const responseRes = result.response.toLowerCase();
            logItem(formatResponseBytesStr(responseRes), path, "response");
        }
    }

    form.querySelector('button[type="submit"]')?.removeAttribute("disabled");
    spinner.classList.add("d-none");
    submitEls.classList.remove("d-none");
}

function logItem(
    request: string,
    path: string,
    direction: "request" | "response",
    isError: boolean = false,
): void {
    const time = new Date().getTime();
    const timeStr = moment(time).format(
        `${logDateFormat}.${new Date(time).getMilliseconds().toString().padStart(3, "0")}`,
    );

    const directionStr = direction === "response" ? "&lt;" : "&gt;";
    const itemClass = isError ? "error" : "normal";
    const str = `<span class="timing">${timeStr} @${path}</span> ${directionStr} <span class="${itemClass}">${request}</span>`;

    const item = document.createElement("div");
    item.classList.add("response-item");
    item.innerHTML = str;

    document.querySelector(`#responses-list`)!.appendChild(item);
}

function parseRequestsText(text: string, form: HTMLElement): number[][] | undefined {
    const rawReqs = text.split("\n").filter((s) => !s.startsWith("#") && s.trim() !== "");

    const reqTextEl = form.querySelector(`#modbus-request`)!;
    const errorTextEl = form.querySelector(`#error-request`)!;
    const cleanReqs = [];
    for (const req of rawReqs) {
        const cleanReq = req.replace(/\W/g, "").toLowerCase();
        // check a-f
        const errorCharacters = cleanReq.match(/[^a-f0-9]/g);
        if (errorCharacters?.length) {
            reqTextEl.classList.add("is-invalid");
            errorTextEl.innerHTML = `Error request: ${req}. The request cannot contain: ${errorCharacters.join(
                ",",
            )}`;
            return;
        }
        // check parity
        if (cleanReq.length % 2) {
            reqTextEl.classList.add("is-invalid");
            errorTextEl.innerHTML = `Error request: ${req}. The request must have an even number of bytes.}`;
            return;
        }
        cleanReqs.push(cleanReq);
    }
    errorTextEl.innerHTML = "";
    reqTextEl.classList.remove("is-invalid");

    const result: number[][] = [];
    for (let reqStr of cleanReqs) {
        const bytes: number[] = [];
        while (reqStr.length > 0) {
            bytes.push(Number(`0x${reqStr.slice(0, 2)}`));
            reqStr = reqStr.substring(2);
        }
        result.push(bytes);
    }

    if (result.length < 1) {
        reqTextEl.classList.add("is-invalid");
        errorTextEl.innerHTML = `Empty request. Please fill the data field.`;
    }

    return result;
}

function setInterfaceOptions(
    container: HTMLElement,
    settingsData: SettingsData,
    stateData: StateData,
): void {
    const selectEl = container.querySelector(`#interface-select`) as HTMLSelectElement;

    if (stateData.ovbState) {
        const option = document.createElement("option");
        option.value = "opcb||self";
        option.text = "OPCB (Modbus TCP)";
        selectEl.appendChild(option);
    }

    for (let i = 0; i <= 3; i++) {
        const isEnabled = stateData[`mbtcpCli${i}Enabled`] as ModbusInterfaces;
        const netif = stateData[`mbtcpCli_${i}Netif`] as number;
        if (!isEnabled || (netif !== 2 && netif !== 3)) {
            continue;
        }
        const option = document.createElement("option");
        const ipPort =
            // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
            `${settingsData[`mbtcpCli${i}RemoteServerIp`]}:` +
            // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
            `${settingsData[`mbtcpCli${i}RemoteServerPort`]}`;
        const mode = netif === 2 ? "Ethernet" : "WIFI";
        option.value = `mbtcp||${ipPort}`;
        option.text = `Modbus Client: ${mode} ${ipPort}`;
        selectEl.appendChild(option);
    }

    for (let i = 0; i <= 3; i++) {
        const devName = stateData[`rs485${i}DevName`] as string | undefined;
        const mode = stateData[`rs485${i}Mode`] as Rs485Modes;
        const status = stateData[`rs485${0}Status`] as Rs485Status;
        if (!devName || (mode !== 0 && mode !== 1) || !status) {
            continue;
        }
        const option = document.createElement("option");
        option.value = `rs485||${devName}`;
        option.text = `${devName} (${getRS485ModeName(mode)})`;
        selectEl.appendChild(option);
    }

    const savedValue = sessionStorage.getItem(`modbus-interface`);
    if (savedValue) {
        selectEl.value = savedValue;
    }
}

export function getInterfaceTypeAndPath(
    container: HTMLElement,
    stateData: StateData,
): { type: "tcp" | "rtu" | "ascii"; path: string } {
    const [reqType, path] = (
        container.querySelector(`#interface-select`) as HTMLSelectElement
    ).value.split("||");

    if (reqType === RequestType.Opcb || reqType === RequestType.Mbtcp) {
        return {
            type: "tcp",
            path,
        };
    }

    if (reqType === RequestType.Rs485 && stateData.rs4850Mode === 1) {
        return { type: "ascii", path };
    }

    return { type: "rtu", path };
}
