import { showGlobalSpinner } from "../spinner";
import { catchAsync } from "../help";
import { submit } from "../data/submit";

export function rigRebootDevice(): void {
    document.querySelector(`#reboot-device-btn`)?.addEventListener(
        "click",
        catchAsync(async () => {
            const conf = confirm(
                "Confirm reboot. The connection to the device will be lost for several minutes after this action.",
            );
            if (!conf) {
                return;
            }
            await rebootDevice();
        }),
    );
}

async function rebootDevice(): Promise<void> {
    showGlobalSpinner(60);
    sessionStorage.setItem("isReboot", "true");

    await submit("./api/cmd.json?cmd_abort_all");
}
